.victory-screen {
  color: #333;
  font-family: 'Baloo 2', cursive;
  font-weight: bold;
  height: 10vh;
  margin-top: 2vh;
  margin-bottom: 30px;

  animation: grow-anim 0.7s ease-out forwards;
}


@keyframes grow-anim {
  from {
    height: 0vh;
  }

  to {
    height: 10vh;
  }
}