.hearts-and-reset {
    position: relative; /* Contenedor relativo para que el botón de reinicio se posicione dentro de este */
    display: flex;
    justify-content: center; /* Centra los corazones horizontalmente */
    align-items: center; /* Alinea verticalmente los corazones */
    margin-bottom: 20px; /* Espaciado respecto al scroll-container */
    margin-top: 20px;
    width: 100%;
}

.hearts-container {
    display: flex;
    gap: 10px; /* Espaciado entre los corazones */
}

.hearts-container .MuiSvgIcon-root {
    color: transparent !important; /* Hace que el relleno del corazón sea transparente */
    font-size: 2rem; /* Tamaño de los corazones */
    stroke: #333; /* Color del borde */
    stroke-width: 1.5px; /* Grosor del borde */
}

.reset-icon {
    position: absolute; /* Posiciona el botón de reinicio de forma absoluta */
    right: 25%; /* Alinea el botón a la derecha del contenedor */
    top: 50%; /* Centra verticalmente el botón dentro del contenedor */
    transform: translateY(-50%); /* Ajusta el centrado vertical */
    color: #333; /* Color del ícono de reinicio */
    cursor: pointer; /* Cambia el cursor al pasar sobre el ícono */
    transition: transform 0.2s ease; /* Animación al pasar el mouse */
}
